<template>
  <v-card
    flat
    class=""
  >
    <!-- tabs -->
    <v-tabs
      v-model="tab"
      show-arrows
    >
      <v-tab
        v-for="tab in tabs"
        :key="tab.icon"
      >
        <v-icon
          size="30"
          class="me-3"
        >
          {{ tab.icon }}
        </v-icon>
        <span>{{ tab.title }}</span>
      </v-tab>
    </v-tabs>

    <!-- tabs item -->
    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-alert
          v-show="showAlert"
          dense
          text
          type="success"
        >
          Successfully marked purchase order as <strong>In Transit</strong>. You will be automatically
          redirected in a bit.
        </v-alert>
        <v-form class="multi-col-validation">
          <v-card-text class="pt-5">
            <v-row>
              <v-col cols="6">
                <v-img
                  class="mt-10"
                  aspect-ratio="1.7"
                  max-height="250"
                  contain
                  lazy-src="https://picsum.photos/id/11/10/6"
                  src="https://picsum.photos/id/11/500/300"
                ></v-img>
              </v-col>
              <v-col cols="6">
                <v-card-text class="mt-10">
                  <v-simple-table
                    fixed-header
                  >
                    <v-subheader>
                      Subheader
                    </v-subheader>
                    <template v-slot:default>
                      <thead>
                        <tr>
                          <th class="text-left">
                            Material Name
                          </th>
                          <th class="text-left">
                            Requested Quantity
                          </th>
                          <th>
                            Supplier
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="(item, key) in orderItems"
                          :key="item.name"
                        >
                          <td>{{ item.name }}</td>
                          <td>{{ item.quantity }}</td>
                          <td>{{ item.supplier_name }}</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-card-text>
              </v-col>
              <v-col cols="12" class="mt-10">
                <card-supplier-details
                  title="Supplier Details"
                  :details="supplier"
                />
              </v-col>
              <v-col
                cols="12"
                class="mt-6"
              >
                <h2 class="font-weight-black mb-2 text-uppercase">Payment Details</h2>
                <v-divider />
              </v-col>
              <v-col
                cols="6"
              >
                <v-text-field
                  v-model="optionsLocal.fx_number"
                  outlined
                  dense
                  label="FX Number"
                  :readonly="mode === 'show'"
                  hide-details="auto"
                  :persistent-placeholder="mode === 'show'"
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  v-model.lazy="optionsLocal.conversion_rate"
                  prefix="P"
                  outlined
                  dense
                  label="Current Conversion Rate"
                  :readonly="mode === 'show'"
                  hide-details="auto"
                  :persistent-placeholder="mode === 'show'"
                  @input="computePercentages"
                ></v-text-field>
              </v-col>
              <v-col>
                <span>
                  Current Paid Amount: ${{ formatPrice(optionsLocal.total) }}
                </span>
              </v-col>
              <v-col>
                <span>
                  Remaining Balance: ${{ formatPrice(balanceToPay) }}
                </span>
              </v-col>
              <v-col cols="3">
                <span>Converted Amount To Pay</span>
              </v-col>
              <v-col
                cols="2"
              >
                <span class="font-weight-black text-base">Amount in USD ${{ dollarPercentageToPay }}</span>
              </v-col>
              <v-col
                cols="2"
              >
                <span class="font-weight-black text-base">Amount in PHP P{{ pesoPercentageToPay }}</span>
              </v-col>
              <v-col cols="4">
                <v-text-field
                  v-model="pesoPercentageToPay"
                  prefix="PHP"
                  outlined
                  dense
                  label="Balance Amount"
                  :readonly="mode === 'show'"
                  hide-details="auto"
                  :persistent-placeholder="mode === 'show'"
                >
                </v-text-field>
              </v-col>
            </v-row>
          </v-card-text>

          <v-card-text class="mt-10">
            <v-btn
              outlined
              class="mr-3"
              color="primary"
              @click="save"
            >
              Mark as In Transit
            </v-btn>
            <v-btn
              outlined
              type="reset"
              @click.prevent="goBack"
            >
              Back to List
            </v-btn>
          </v-card-text>
        </v-form>
      </v-tab-item>
      <v-tab-item>
        <purchase-order-logs :order-id="id"></purchase-order-logs>
      </v-tab-item>
      <v-tab-item>
        <purchase-order-payment-logs :order-id="id"></purchase-order-payment-logs>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>

<script>
import {
  computed, nextTick, ref, watch, onMounted,
} from '@vue/composition-api'
import router from '@/router'
import store from '@/store'
import {
  IN_TRANSIT_PURCHASE_ORDER,
  GET_SUPPLIER,
} from '@/store/actions.type'
import PurchaseOrdersService from '@/utils/services/purchase-orders.service'
import { mdiBookInformationVariant, mdiClipboardListOutline, mdiCurrencyUsd } from '@mdi/js'
import PurchaseOrderLogs from '@/views/pages/purchase-orders/purchase-order/logs/PurchaseOrderLogs'
import PurchaseOrderPaymentLogs
  from '@/views/pages/purchase-orders/purchase-order/logs/PurchaseOrderPaymentLogs';
import CardSupplierDetails from '@/views/pages/purchase-orders/component/CardSupplierDetails.vue'

export default {
  components: {
    PurchaseOrderPaymentLogs,
    PurchaseOrderLogs,
    CardSupplierDetails
  },
  setup() {
    const { id } = router.history.current.params
    const errors = computed(() => store.getters.errors)
    const optionsLocal = ref({})
    const supplier = ref({})
    const showAlert = ref(false)
    const orderItems = ref([])
    const tab = ref('')
    const price = ref('')
    const dollarPercentageToPay = ref('')
    const pesoPercentageToPay = ref('')
    const balanceToPay = ref('')

    onMounted(async () => {
      await PurchaseOrdersService.find(id)
        .then(response => {
          const { data } = response.data
          console.log(data)
          optionsLocal.value = {
            description: data.order.description,
            fx_number: data.order.fx_id,
            eta: data.order.eta,
            total: data.order.total,
            paid_amount: data.order.paid_amount,
            items: {},
          }

          balanceToPay.value = (data.order.total_amount_payable - data.order.total).toString()
          price.value = balanceToPay.value

          supplier.value = data.supplier
          orderItems.value = data.items
        })
    })

    const computePercentages = () => {
      const form = optionsLocal.value
      dollarPercentageToPay.value = parseInt(form.total).toFixed(2).toString()
      pesoPercentageToPay.value = parseInt(form.total * form.conversion_rate).toFixed(2).toString()
    }

    const goBack = () => {
      router.push('/purchase-orders')
    }

    const resolveFormSuccess = () => {
      showAlert.value = true
      setTimeout(() => {
        goBack()
      }, 5000)
    }

    const formatPrice = value => parseFloat(value)
      .toFixed(2)
      .toString()
      .replace(/\B(?=(\d{3})+(?!\d))/g, ',')

    watch(
      price,
      value => {
        const result = formatPrice(value)
        nextTick(() => price = result)
      },
      { immediate: true },
    )

    const save = () => {
      const currentForm = optionsLocal.value
      currentForm.id = router.history.current.params.id
      currentForm.usd_conversion = dollarPercentageToPay.value
      currentForm.peso_conversion = pesoPercentageToPay.value
      currentForm.paid_amount = price.value

      store.dispatch(IN_TRANSIT_PURCHASE_ORDER, currentForm).then(data => {
        if (data != null) {
          resolveFormSuccess()
        }
      })
    }

    // tabs
    const tabs = [
      {title: 'Purchase Order Information', icon: mdiBookInformationVariant},
      {title: 'Purchase Order Logs', icon: mdiClipboardListOutline},
      {title: 'Purchase Order Payment Logs', icon: mdiCurrencyUsd},
    ]

    return {
      id,
      tabs,
      tab,
      optionsLocal,
      orderItems,
      goBack,
      save,
      showAlert,
      errors,
      price,
      formatPrice,
      dollarPercentageToPay,
      pesoPercentageToPay,
      computePercentages,
      balanceToPay,
      supplier,
    }
  },
}
</script>
